@font-face {
  font-family: "Avenir";
  src: url(./fonts/Avenir.ttc);
}

@font-face {
  font-family: "Avenir";
  src: url(./fonts/Avenir-Heavy.ttf);
  font-weight: bold;
}

body {
  font-family: "Avenir", sans-serif;
  font-size: 16px;
  font-weight: 300;
}

.search-field {
  position: relative;
  margin: 20px 0;
}

.search-bar {
  color: #3d4e61;
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  line-height: 22px;
  box-sizing: border-box;
  box-shadow: 0 1px 8px rgba(61, 78, 97, 0.1);
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  width: 100%;
  height: 70px;
  padding: 23px 0 23px 62px;
}

.search-image {
  position: absolute;
  width: 19px;
  top: 25px;
  left: 26px;
}

.search-bar:hover {
  box-shadow: 0 3px 8px rgba(61, 78, 97, 0.2);
}

.search-bar:focus {
  background: linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
  border-radius: 3px;
  font-weight: bold;
  line-height: 22px;
  outline: none;
}

.search-bar::placeholder {
  color: inherit;
}

.search-bar-small {
  font-size: 14px;
  line-height: 19px;
  height: 42px;
  padding: 10px 0 10px 33px;
}

.search-image-small {
  width: 11px;
  top: 15px;
  left: 13px;
}
